
import Vue from 'vue';
import { EventBus } from '@/Events.ts';
import ElButton from '@/components/ElButton.vue';

export default Vue.extend ({
	name: 'QusItems',
	components: {
		ElButton
	},
	data() {
		return {
			canSubmit: true,
			stepIndex: 0,
			picked: null as any,
			pickedList: [] as any[],
			qus: [
				{
					title: 'exclusiva para clientes?',
					subtitle: ' uma app',
					message: 'exclusiva para clientes',
					children: [
						{ title: 'Sim'},
						{ title: 'No',
							response: [ { target: 3 } ]
						}
					]
				},
				{
					title: 'ativar cobertura?',
					subtitle: 'serve para',
					message: 'Ativa coberturas',
					children: [
						{ title: 'Sim'},
						{ title: 'No',
							response: [ { target: 2 } ]
						}
					]
				},
				{
					title: 'gerir as aplices?',
					subtitle: 'serve para',
					message: 'exclusiva para clientes',
					children: [
						{ title: 'Sim',
							response: [ { target: 0 } ]
						},
						{ title: 'No',
							response: [ { target: 1 } ]
						}
					]
				}
			]
		}
	},
	computed: {
		stepObj(): any {
			return this.qus[this.stepIndex];
		},
		itemIndex(): any {
			return this.stepObj.children[this.picked].response[0].target;
		},
		isLastItem(): boolean {
			return this.picked !== null &&
			this.stepIndex === this.qus.length - 1;
		},
		isMiddleItems(): boolean {
			return this.picked !== null &&
			this.stepIndex < this.qus.length - 1;
		},
		userResponse(): boolean {
			return this.picked === 1 ? true : false;
		}
	},
	methods: {
		prev() {
			this.stepIndex--
			this.pickedList.pop();
			this.picked = null
		},
		next() {

			if (this.isLastItem) {

				this.pickedList.push(
					{
						message: this.qus[this.stepIndex].message,
						response: this.userResponse
					}
				);

				EventBus.$emit('continue',
					{
						'pageName': 'Cards',
						'itemIndex': this.itemIndex,
						'child': [...this.pickedList]
					},
				);
			}

			this.picked = null
		}
	},
	watch: {
		picked() {

			if (this.isMiddleItems) {

				this.pickedList.push(
					{
						message: this.qus[this.stepIndex].message,
						response: this.userResponse
					}
				);

				if (this.userResponse) {
					EventBus.$emit('continue',
						{
							'pageName': 'Cards',
							'itemIndex': this.itemIndex,
							'child': [...this.pickedList]
						}
					);

				} else if (!this.userResponse) {
					this.stepIndex++
				}

				this.picked = null;

			}

			this.canSubmit = this.picked !== null;
		}
	}
});
