
import Vue from 'vue';
import {EventBus} from '@/Events.ts';

export default Vue.extend({
	name: 'Navigation',
	data() {
		return {
			pageName: null
		}
	},
	methods: {
		intro() {
			EventBus.$emit('continue', {
				'pageName': 'Intro',
				'itemIndex': null
			});
		},
		qusItem() {
			EventBus.$emit('continue', {
				'pageName': 'QusItems',
				'itemIndex': null
			});
		}
	},
	mounted() {
		EventBus.$on('continue', (data: any) => {
			this.pageName = data.pageName;
		});
	}
});
