
import Vue from 'vue';
import { EventBus } from '@/Events.ts';
import ElButton from '@/components/ElButton.vue';

export default Vue.extend ({
	name: 'Cards',
	components: {
		ElButton
	},
	props: ['itemIndex'],
	data() {
		return {
			slideControlIndex: 0,
			selected: this.itemIndex,
			hovered: this.itemIndex,
			cards: [
				{
					name: 'Product',
					description: 'Uma app Product  onde existe gesto dos produtos e da relao com a Fidelidade',
					iconUrl: require('../assets/icon/product.svg'),
					slides: [
						{
							title: 'MyFidelidade',
							description: 'A app MyFidelidade  uma forma inovadora de gerir os seus seguros.',
							thumbUrl: require('../assets/icon/myfid.png')
						}
					]
				},
				{
					name: 'Product as Service',
					description: 'Uma app Product as a Service permite utilizar coberturas ou servios de um seguro',
					iconUrl: require('../assets/icon/proasservice.svg'),
					slides: [
						{
							title: 'My Savings',
							description: 'Defina a melhor opo de poupana/investimento para chegar ao objetivo.',
							thumbUrl: require('../assets/icon/mysav.png')
						}
					]
				},
				{
					name: 'Service',
					description: 'Uma app Service  onde esto disponveis servios da Fidelidade',
					iconUrl: require('../assets/icon/service.svg'),
					slides: [
						{
							title: 'Medicina Online',
							description: 'A Multicare oferece mdicos especialistas atravs da vdeo consulta.',
							thumbUrl: require('../assets/icon/multi.png')
						},
						{
							title: 'Assistance',
							description: 'Os clientes Fidelidade podem pedir assistncia automvel, em viagem',
							thumbUrl: require('../assets/icon/assis.png')
						}
					]
				},
				{
					name: 'Beyond Insurance',
					description: 'Uma app Beyond Insurance  para criar awareness e acessvel a no clientes.',
					iconUrl: require('../assets/icon/beyond.svg'),
					slides: [
						{
							title: 'Drive',
							description: ' uma nova forma de conhecer e melhorar os seus comportamentos',
							thumbUrl: require('../assets/icon/drive.png')
						},
						{
							title: 'My Pets',
							description: 'Desenvolvida por uma equipa de veterinrios, e existe para ajudar os',
							thumbUrl: require('../assets/icon/pet.png')
						}
					]
				}
			]
		}
	},
	methods: {
		confirm(): any {
			EventBus.$emit('continue', {'pageName': 'Result', 'itemIndex': this.selected} );
		},
		next() {
			if (this.slideControlIndex <
			this.cards[this.hovered].slides.length - 1) {
				this.slideControlIndex++
			}
		},
		prev() {
			if (this.slideControlIndex > 0) {
				this.slideControlIndex--
			}
		}
	},
	watch: {
		hovered() {
			this.slideControlIndex = 0
		}
	}
})
